import React, { useState, useEffect} from 'react'
import ReactDOM from 'react-dom'
import './index.css'; 
import About from './about.js';
import Projects from './projects.js';
import Contact from './contact.js';

const about = <About/>;
const projects = <Projects/>;
const contact = <Contact/>;
const pages = [
  {
    id: 0,
    name: 'About',
    pagenm: about
  },
  {
    id: 1,
    name: 'Projects',
    pagenm: projects
  },
  {
    id: 2,
    name: 'Contact',
    pagenm: contact
  }
]
const Link = (props) => (
  <a onClick={props.handleClick}>{props.text}</a>
)

const Footer = () => {
  return (
    <React.Fragment>
    <div className="footer">
      <div className="container">
        <div className ="row center-xs center-sm center-md center-lg">
          <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
            <p> <b>© 2020 Aapo Hyyryläinen</b> </p>
          </div>
        </div>
      </div>
    </div>
    </React.Fragment>
  )
}
const Content = () => {
  return (
    <div className="react-container">
    <About/>;
    </div>
)}
//TOGGLES
const toggleDarkmode = () => {
  var element = document.body;
  element.classList.toggle("dark");
}
const activateLink = (props) => {
  var linklist = document.getElementsByClassName("links");
  var i;
  for (i = 0; i < linklist.length; i++) {
  linklist[i].classList.remove("active-link");
  document.getElementById(pages[i].name).classList.add("hidden");
  }
  linklist[props].classList.add("active-link");
  document.getElementById(pages[props].name).classList.add("active")
  document.getElementById(pages[props].name).classList.remove("hidden")
}
//APP
const App = () => {
  const [pagenr, setPage] = useState(0);
  React.useEffect(() => {
  activateLink(0)
      }, []);
  
  return (
    <React.Fragment>
    <header>
    <div className="container">
      <div className="row">
        <div className="col-md-12">
          <svg onClick = {toggleDarkmode} id="logobox" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 282.39 275.5"><line x1="141.07" y1="41.29" x2="71.63" y2="234.21"/><line x1="141.07" y1="41.29" x2="210.5" y2="234.21"/><line x1="141.07" y1="137.75" x2="40.33" y2="137.75"/><line x1="241.93" y1="137.75" x2="141.2" y2="137.75"/><line x1="40.33" y1="63.03" x2="40.33" y2="206.51" /><line x1="242.07" y1="66.01" x2="242.07" y2="209.49" /><ellipse cx="141.2" cy="137.75" rx="138.7" ry="135.25"/></svg>
             <ul className ="nav-link-menu">
              {pages.map(page => <li className="links" key={page.id}><Link handleClick={() => {setPage(page.id); activateLink(page.id)}} text={page.name}></Link></li>)}
            </ul>
        </div>
      </div>
    </div>
    </header>
    {pages.map(page => <div id={page.name} key={page.id +10 }>{page.pagenm}</div>)}
    </React.Fragment>
  )
}

ReactDOM.render(<App/>, document.getElementById('content-container'))
ReactDOM.render(<Footer/>, document.getElementById('footer'))
