import React from 'react'

function topFunction() {
  document.body.scrollTop = 0;
  document.documentElement.scrollTop = 0;
}

class Projects extends React.Component {
  render() { 
  return <React.Fragment><div className="container">
    <div className="row content">
      <div className="col-xs-4">
        <div className="textbox heading">
          <h2>BONK</h2>
        </div>
        <div className="textbox">
          <p>
            BONK is a 3D top-down shooter solo-developed using Unity and C#.
            <br></br><br></br>
            During this project I also dabbled in Unity's shader graph and HDRP functionality.
          </p>
        </div>
      </div>
      <div className="col-xs-4">
          <img src="./dump (2).png"/>
      </div>
    </div>
  </div>
  <div className="container">
      <div className="row content">
        <div className="col-xs-4">
          <img src="./Harava.gif"/>
        </div>
        <div className="col-xs-4">
          <div className="textbox heading">
            <h2>HARAVA</h2>
          </div>
          <div className="textbox">
            <p>
              Harava is a 2D Pixel Art adventure game concept I made as part of my studies.
              <br></br><br></br>
              The project also included creating my own art and music in addition to creating a GDD.
            </p>
          </div>
        </div>
      </div>
    </div>
    <div className="container">
      <div className="row content">
        <div className="col-xs-12">
            <h1 onClick={topFunction}>That's all for now!</h1>
        </div>
      </div>
    </div>
  </React.Fragment>
  }
}
export default Projects;